import React from "react"
import '../styles/components/header.scss'
import { Link, graphql, StaticQuery } from "gatsby"
import Img from 'gatsby-image';
import { AnchorLink } from "gatsby-plugin-anchor-links";

const Header = () => {

  const toggleBox = () =>{
    document.getElementsByClassName("burger-dropdown")[0].classList.toggle("is-hidden");
    document.getElementsByClassName("burger-dropdown")[0].classList.toggle("animated");
    const section = document.getElementsByTagName("section");
    for (const item of section) {
      item.classList.toggle("blurry");
    }
  }

  return(
  <header>
    <nav className="navbar is-fixed-top is-primary" role="navigation" aria-label="main navigation">
      <div className="navbar-brand">
        <div className="img-wrapper">
        <StaticQuery
                query={graphql`
                  query imgLogo {
                      file(name: {eq: "logoBig"}) {
                          childImageSharp {
                              fluid {
                                  ...GatsbyImageSharpFluid
                              }
                          }
                      }
                  }
              `}

                render={data=> (
                  <Img className="img-logo" style={{position: "absolute"}} fluid={data.file.childImageSharp.fluid}/>
                )}
        />
        </div>
        <div className="burger-wrapper" onClick={toggleBox}>
          <div role="button" className="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </div>
          <div className="burger-dropdown is-hidden is-hidden-desktop">
              <AnchorLink to="/" className="navbar-item" onClick={toggleBox}>
                Home
              </AnchorLink>

              <AnchorLink  to="#portfolio" className="navbar-item" title="Portfolio" onClick={toggleBox}>
              Portfolio
              </AnchorLink>

              <Link to="/about" className="navbar-item" onClick={toggleBox}>
                About me
              </Link>

              <AnchorLink  to="#contact" className="navbar-item" title="Contact" onClick={toggleBox}>
              Contact
              </AnchorLink>
              <span className="burger-box-x"></span>
          </div>
        </div>
      </div>

      <div id="navbarBasicExample" className="navbar-menu">
        <div className="navbar-end">
          <Link to="/" className="navbar-item">
            Home
          </Link>

          <AnchorLink  to="#portfolio" className="navbar-item" title="Portfolio">
          Portfolio
          </AnchorLink>

          <Link to="/about" className="navbar-item">
            About me
          </Link>

          <AnchorLink  to="#contact" className="navbar-item" title="Contact">
          Contact
          </AnchorLink>

        </div>
      </div>
    </nav>
  </header>
  )
}

export default Header
